.tabs {
	&__list {
		display: flex;
		align-items: center;
	}

	&__item+&__item {
		margin-left: 25px;

		@media (max-width: 767px) {
			margin-left: 14px;
		}
	}

	&__btn {
		color: #ADADB0;
		font-weight: 800;
		font-size: 42px;
		text-align: center;
		transition: all 0.1s ease-in-out;

		@media (max-width: 767px) {
			font-size: 24px;
		}

		&:hover {
			color: #9e9ea1;
		}

		&--active {
			color: #222025;
			pointer-events: none;
		}
	}

	&__content {
		display: none;

		&--active {
			display: block;
		}
	}
}