.footer {
	position: absolute;
	width: 100%;
	bottom: 40px;

	&--auth {
		.footer__info {
			a {
				color: #fff;
			}

			p {
				color: #fff;
			}
		}

		.footer__by {
			color: #fff;

			a {
				color: #fff;
			}
		}
	}

	&--personal {
		.footer__info {
			a {
				color: #211F2E;
				opacity: 0.4;

				&:hover {
					opacity: 1;
				}
			}

			p {
				color: #211F2E;
				opacity: 0.4;
			}
		}

		.footer__by {
			color: #211F2E;
			opacity: 0.4;

			a {
				color: #211F2E;
			}
		}
	}

	@media (max-width: 767px) {
		bottom: 20px;
	}

	&__content {
		max-width: 1350px;
		margin: auto;
		padding: 0 20px;
		display: flex;
		justify-content: space-between;

		@media (max-width: 575px) {
			flex-direction: column;
		}
	}

	&__left {
		display: flex;

		@media (max-width: 767px) {
			font-size: 14px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
			flex-direction: column;
		}
	}

	&__info {
		a {
			font-weight: 700;
			transition: all 0.3s ease;

			&:hover {
				color: $color-primary;
			}
		}

		p {
			margin-top: 6px;
			font-size: 12px;

		}
	}

	&__info+&__info {
		margin-left: 78px;

		@media (max-width: 767px) {
			margin-left: 40px;
		}

		@media (max-width: 575px) {
			margin: 24px 0 0 0;
		}
	}

	&__by {
		font-weight: 700;

		@media (max-width: 767px) {
			font-size: 14px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
			margin-top: 24px;
		}

		a {
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}
	}
}