html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	min-width: 320px;
	font-family: "Korzinka",
		sans-serif;
}

body.lock {
	overflow: hidden;
}

input {
	border: none;

	&::placeholder {
		opacity: 1;
	}
}

a {
	text-decoration: none;
}

textarea {
	border: none;
	resize: none;

	&::placeholder {
		opacity: 1;
	}
}

img {
	max-width: 100%;
}

ol,
ul {
	list-style: none;
	margin: 0;
	padding: 0;
}

button {
	border: none;
	padding: 0;
	background: transparent;
	cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
}

.wrapper {
	position: relative;
	width: 100%;
	min-height: 100%;
	overflow: hidden;
	display: flex;
	flex-direction: column;
}

.main {
	flex: 1 1 auto;
}

.container {
	margin: 0 auto;
	padding: 0 20px;
	max-width: 1200px;

	@media (max-width: 1200px) {
		max-width: 970px;
	}

	@media (max-width: 991px) {
		max-width: 750px;
	}

	@media (max-width: 767px) {
		max-width: none;
	}
}

$color-primary: #E4002B;

.title {
	font-weight: 800;
	font-size: 55px;
	line-height: 100%;
	color: #222025;

	@media (max-width: 991px) {
		font-size: 42px;
		line-height: 42px;
	}
}

.caption {
	margin-top: 48px;
}

.subtitle {
	font-weight: 700;
	font-size: 32px;
	line-height: 100%;
	color: #222025;

	@media (max-width: 767px) {
		font-size: 25px;
		line-height: 28px;
	}
}

.desc {
	margin-top: 16px;
	line-height: 20px;
	letter-spacing: -0.01em;
	color: #ADADB0;
}

.btn {
	display: inline-block;
	position: relative;
	font-size: 19px;
	color: #fff;
	z-index: 1;
	transition: all 0.3s ease;

	@media (max-width: 991px) {
		font-size: 16px;
	}

	@media (max-width: 767px) {
		font-size: 16px;
		padding-top: 19px;
		padding-bottom: 19px;
	}

	&--disabled {
		opacity: .5;
		pointer-events: none;
	}

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #e4002b;
		transform: skew(-15deg);
		border-radius: 40px;
		z-index: -1;
		transition: all .3s ease;
	}

	&:hover:before {
		background-color: #c30025;
	}
}

.status {
	position: relative;
	padding-left: 30px;

	@media (max-width: 1200px) {
		font-size: 14px;
	}

	&::before {
		content: "";
		position: absolute;
		top: 50%;
		left: 0;
		right: 0;
		bottom: 0;
		transform: skew(-20deg) translateY(-50%);
		border-radius: 40px;
		transition: all .3s ease;
		width: 18px;
		height: 18px;
	}

	&--approved {
		&::before {
			background-color: #A1E565;
		}
	}

	&--rejected {
		&::before {
			background-color: $color-primary;
		}
	}

	&--archived {
		&::before {
			background-color: #ADADB0;
		}
	}
}

.checkbox {
	display: flex;
	align-items: center;
	font-size: 13px;
	cursor: pointer;

	@media (max-width: 767px) {
		padding-left: 20px;
	}

	input {
		display: none;

		&:checked+span {
			background-image: url("../img/svg/checkmark.svg");
			border: none;
		}
	}

	span {
		display: block;
		background-size: contain;
		background-position: center;
		background-repeat: no-repeat;
		width: 100%;
		max-width: 24px;
		height: 25px;
		border: 2px solid #DCDCDF;
		border-radius: 7px;
	}

	i {
		font-size: 19px;
		color: #222025;
		font-style: normal;
		padding-left: 10px;

		@media (max-width: 767px) {
			font-size: 14px;
		}
	}
}

.form-group {
	position: relative;

	&::before {
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: #F7F7F9;
		transform: skew(-15deg);
		border-radius: 40px;
		z-index: -1;
	}

	&--error {
		&:before {
			border: 1px solid $color-primary;
		}
	}

	&__input {
		position: relative;
		background: transparent;
		outline: none;
		width: 100%;
		padding: 25px 30px;
		font-size: 19px;
		line-height: 24px;
		color: #222025;

		&::placeholder {
			color: #ADADB0;
		}

		@media (max-width: 767px) {
			font-size: 16px;
			padding: 17px 15px;
		}
	}

	&__error {
		position: absolute;
		left: 30px;
		bottom: 8px;
		font-size: 10px;
		color: #F13434;

		@media (max-width: 767px) {
			left: 15px;
			bottom: 5px;
		}

		@media (max-width: 355px) {
			font-size: 9px;
		}
	}

	&__textarea {
		position: relative;
		background: transparent;
		outline: none;
		width: 100%;
		padding: 25px 30px;
		font-size: 19px;
		line-height: 24px;
		color: #222025;
		max-height: 170px;

		&::placeholder {
			color: #ADADB0;
		}

		@media (max-width: 575px) {
			font-size: 14px;
		}
	}

	&__calendar {
		background-image: url('../img/svg/arrow-down-red.svg');
		background-size: 20px;
		background-position-x: 90%;
		background-position-y: 50%;
		background-repeat: no-repeat;
	}

	&__inner {
		margin-bottom: 24px;
	}
}

.form-file {
	margin-top: 32px;
	display: flex;
	align-items: center;

	@media (max-width: 991px) {
		justify-content: center;
	}

	&__items {
		display: grid;
		grid-template-columns: repeat(5, 1fr);
		grid-gap: 10px;

		@media (max-width: 1200px) {
			grid-template-columns: repeat(4, 1fr);
		}

		@media (max-width: 991px) {
			grid-template-columns: repeat(5, 1fr);
		}

		@media (max-width: 767px) {
			grid-template-columns: repeat(4, 1fr);
		}

		@media (max-width: 575px) {
			grid-template-columns: repeat(3, 1fr);
		}
	}

	&__img {
		position: relative;
		width: 120px;
		height: 120px;
		border-radius: 24px;
		transition: all 0.3s ease;

		&:hover {
			.form-file__close {
				opacity: 1;
				visibility: visible;
			}
		}

		@media (max-width: 575px) {
			width: 80px;
			height: 80px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 24px;
		}

		&--main {
			border: 2px solid $color-primary;
		}
	}

	&__close {
		position: absolute;
		top: -15px;
		right: -5px;
		opacity: 0;
		visibility: hidden;
		transition: all 0.3s ease;
	}

	&__label {
		width: 120px;
		height: 120px;
		display: block;
		cursor: pointer;
		transition: all 0.3s ease;

		@media (max-width: 575px) {
			width: 80px;
			height: 80px;
		}

		&:hover {
			opacity: 0.8;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 24px;
		}
	}

	&__input {
		opacity: 0;
		position: absolute;
		z-index: -1;
	}

	&__box {
		margin-left: 24px;

		@media (min-width: 768px) {
			max-width: 405px;
		}

		@media (max-width: 575px) {
			margin-left: 15px;
		}
	}

	&__subtitle {
		font-size: 19px;
		line-height: 24px;
		color: #222025;
		margin-bottom: 8px;

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__text {
		line-height: 18px;
		color: #222025;

		@media (max-width: 575px) {
			margin-top: 5px;
			font-size: 14px;
		}
	}
}

.form-box {
	@media (min-width: 576px) {
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 20px;
	}

	.form-group__select {
		@media (min-width: 576px) {
			background-position-x: 88%;
		}
	}
}

.top {
	margin-top: 32px;
	display: flex;
	justify-content: space-between;
	align-items: center;

	@media (max-width: 767px) {
		flex-direction: column;
		align-items: flex-start;
	}
}

.step {
	font-style: italic;
	font-size: 25px;
	line-height: 28px;
	text-align: right;
	color: #222025;

	@media (max-width: 767px) {
		margin-top: 20px;
	}
}

.back {
	position: relative;
	color: $color-primary;
	padding-left: 20px;
	transition: all 0.3s ease;

	&:hover {
		opacity: 0.8;
	}

	&::before {
		content: '';
		background-image: url('../img/svg/arrow-down-red.svg');
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		position: absolute;
		top: 50%;
		left: 0;
		width: 12px;
		height: 12px;
		transform: translateY(-50%) rotate(90deg);
	}
}

.buttons {
	margin-top: 77px;

	@media (max-width: 575px) {
		display: flex;
		text-align: center;
	}

	&__btn {
		padding: 25px 40px;

		@media (max-width: 575px) {
			width: 100%;
			padding: 25px 20px;
		}

		@media (max-width: 375px) {
			padding: 25px 5px;
			font-size: 14px;
		}

		&--back {
			color: #222025;

			&:hover {
				&:before {
					background-color: #e5e5e5;
				}
			}

			&::before {
				background-color: #ededed;
			}
		}
	}

	&__btn+&__btn {
		margin-left: 20px;

		@media (max-width: 575px) {
			margin-left: 5px;
		}
	}
}

.inner {
	margin-top: 56px;

	@media (max-width: 575px) {
		margin-top: 30px;
	}

	&__grid {
		margin-top: 30px;
		display: grid;
		grid-gap: 25px;

		&--3 {
			grid-template-columns: repeat(3, 250px);

			@media (max-width: 767px) {
				grid-template-columns: repeat(2, 1fr);
			}

			@media (max-width: 575px) {
				grid-template-columns: repeat(1, 1fr);
			}
		}

		&--2 {
			grid-template-columns: repeat(2, 250px);

			@media (max-width: 575px) {
				grid-template-columns: repeat(1, 1fr);
			}
		}
	}

	.form-group {
		margin-top: 30px;
	}

	.caption {
		margin-top: 0;
	}

	.checkbox {
		@media (max-width: 767px) {
			padding-left: 0;
		}
	}
}

.dropdown {
	position: relative;

	&.active {
		.dropdown__list {
			opacity: 1;
			visibility: visible;
		}
	}

	&__current {
		position: relative;
		padding: 25px 30px;

		&::before {
			content: '';
			background-image: url('../img/svg/arrow-down-red.svg');
			background-size: 20px;
			background-position: center;
			background-repeat: no-repeat;
			position: absolute;
			top: 50%;
			right: 30px;
			transform: translateY(-50%);
			width: 20px;
			height: 20px;
			z-index: 11;
		}


		@media (max-width: 767px) {
			font-size: 16px;
			padding: 17px 55px 17px 30px;
		}

		@media (max-width: 575px) {
			padding-left: 20px;
		}
	}

	&__desc {
		position: relative;
		z-index: 11;
		font-size: 19px;
		line-height: 24px;
		color: #ADADB0;

		@media (max-width: 575px) {
			font-size: 14px;
			line-height: 15px;
		}
	}

	&__list {
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: 0;
		left: 0;
		background-color: #fff;
		box-shadow: 0px 8px 38px rgba(34, 32, 37, 0.08);
		width: 100%;
		padding: 65px 30px 25px;
		border-radius: 40px;
		z-index: 10;
		transition: all 0.3s ease;

		@media (max-width: 575px) {
			padding: 55px 20px 25px;
		}

		li {
			border-bottom: 1px solid #DCDCDF;

			&:first-child {
				border-top: 1px solid #DCDCDF;
			}

			&:last-child {
				border-bottom: none;
			}
		}

		&--grid {
			display: grid;
			grid-template-columns: repeat(2, 1fr);

			@media (max-width: 575px) {
				grid-template-columns: repeat(1, 1fr);
			}

			li {
				border-bottom: none;

				&:nth-child(2) {
					@media (min-width: 576px) {
						border-top: 1px solid #DCDCDF;
					}
				}
			}

			.checkbox span {
				@media (min-width: 576px) {
					right: 15px !important;
				}
			}
		}
	}

	.checkbox {
		position: relative;
		padding: 16px 0;

		span {
			position: absolute;
			right: 0;
			border: none;
			max-width: 11px;
			height: 16px;

			@media (max-width: 767px) {
				right: 15px;
			}

			@media (max-width: 575px) {
				right: 0;
			}
		}

		input:checked+span {
			background-image: url('../img/svg/check.svg');
		}

		input:checked+span+i {
			color: $color-primary;
		}

		i {
			padding-left: 0;
		}
	}
}

.choose {
	margin: 24px 0;
	display: grid;
	grid-template-columns: repeat(3, auto);
	grid-gap: 15px;

	@media (max-width: 1200px) {
		grid-template-columns: repeat(4, auto);
	}

	@media (max-width: 991px) {
		grid-template-columns: repeat(3, auto);
	}

	@media (max-width: 767px) {
		grid-template-columns: repeat(2, auto);
	}

	@media (max-width: 575px) {
		grid-template-columns: repeat(1, auto);
	}

	&__item {
		display: flex;
		align-items: center;
		position: relative;
		color: #fff;
		padding: 12px 35px 12px 15px;
		font-size: 14px;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: $color-primary;
			transform: skew(-15deg);
			border-radius: 40px;
			z-index: -1;
		}
	}

	&__close {
		position: absolute;
		top: 50%;
		right: 10px;
		transform: translateY(-50%);

		img {
			filter: brightness(10);
		}
	}
}