.information {
	padding-top: 143px;

	@media (max-width: 767px) {
		padding-top: 90px;
	}

	&__form {
		display: flex;
		justify-content: space-between;

		@media (max-width: 991px) {
			flex-direction: column;
		}
	}

	&__left {
		@media (min-width: 992px) {
			flex: 0 0 54%;
		}
	}

	.form-group {
		margin-top: 16px;
	}

	&__box {
		display: flex;
	}

	&__checkbox {
		margin-top: 56px;

		.checkbox {
			@media (max-width: 767px) {
				padding-left: 0;
			}

			span {
				max-width: 30px;
				height: 30px;
			}

			i {
				padding-left: 20px;

				@media (max-width: 575px) {
					padding-left: 10px;
				}
			}
		}

		.checkbox+.checkbox {
			margin-top: 30px;
		}
	}

	&__right {
		@media (min-width: 992px) {
			flex: 0 0 30%;
		}

		@media (max-width: 991px) {
			margin-top: 30px;
			order: -1;
		}
	}

	&__content {
		background: #F7F7F9;
		border-radius: 20px;
		padding: 23px;

		@media (max-width: 991px) {
			max-width: 400px;
			margin: auto;
		}

		@media (max-width: 575px) {
			display: none;
		}
	}

	&__header {
		position: relative;
	}

	&__img {
		position: relative;
		width: 100%;
		max-width: 352px;
		height: 239px;
		overflow: hidden;
		transform: skew(-15deg) rotate(-10deg);
		border-radius: 50%;
		transition: all .3s ease;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__price {
		position: absolute;
		top: 120px;
		right: 0;
		width: 100px;
		height: 140px;
		font-weight: 800;
		font-size: 25px;
		line-height: 25px;
		text-align: center;
		letter-spacing: -.43892px;
		color: $color-primary;
		z-index: 1;
		display: flex;
		align-items: center;
		justify-content: center;

		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: #f5e81e;
			border-radius: 50%;
			transform: rotate(-130deg);
			z-index: -1;
		}
	}

	&__body {
		margin-top: 30px;
	}

	&__subject {
		font-size: 24px;
		line-height: 120%;
		color: #222025;

		@media (max-width: 575px) {
			font-size: 18px;
		}
	}

	&__items {
		margin-top: 16px;
	}

	&__item {
		display: flex;
	}

	&__label {
		font-size: 19px;
		line-height: 26px;
		letter-spacing: -0.43892px;
		color: #ADADB0;

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}

	&__value {
		font-size: 19px;
		line-height: 26px;
		letter-spacing: -0.43892px;
		color: #ADADB0;
		margin-left: 5px;

		@media (max-width: 575px) {
			font-size: 16px;
		}
	}
}

.range-information {
	margin-top: 32px;

	&__top {
		display: flex;
		justify-content: space-between;
	}

	&__desc {
		font-size: 19px;
		color: #222025;
	}

	&__value {
		font-weight: 800;
		font-size: 25px;
		line-height: 24px;
		color: #222025;

		@media (max-width: 575px) {
			font-size: 19px;
		}
	}

	&__slider {
		margin-top: 20px;
	}

	.noUi-target {
		position: relative;
	}

	.noUi-horizontal {
		height: 14px;
	}

	.noUi-connect {
		background: #E4002B;
	}

	.noUi-target {
		background: #F0F0F0;
		border-radius: 16px;
	}

	.noUi-horizontal .noUi-handle {
		width: 32px;
		height: 41px;
		right: -17px;
		top: -14px;
		position: absolute;
		cursor: pointer;

		@media (max-width: 575px) {
			width: 25px;
			height: 31px;
			top: -9px;
		}

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			right: 0;
			bottom: 0;
			background-color: #e4002b;
			transform: skew(-15deg);
			border-radius: 50%;
			transition: all .3s ease;
		}
	}

	.noUi-connect {
		will-change: transform;
		position: absolute;
		top: 0;
		right: 0;
		height: 100%;
		width: 100%;
		transform-origin: 0 0;
		transform-style: flat;
		border-radius: 16px;
	}

	.noUi-touch-area {
		height: 100%;
		width: 100%;
	}
}