.footer-info {
	padding: 160px 0 100px;

	@media (max-width: 991px) {
		padding: 80px 0 60px;
	}

	@media (max-width: 575px) {
		padding: 55px 0 50px;
	}

	&__top {
		display: flex;
		justify-content: space-between;

		@media (max-width: 575px) {
			flex-direction: column;
		}
	}

	&__logo {
		width: 420px;
		height: 65px;

		@media (max-width: 991px) {
			width: 226px;
			height: 35px;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__desc {
		max-width: 210px;
		margin-top: 26px;
		font-size: 28px;
		line-height: 120%;
		color: #E4002B;

		@media (max-width: 991px) {
			font-size: 24px;
		}
	}

	&__socials {
		display: flex;

		@media (max-width: 575px) {
			margin-top: 26px;
		}

		a {
			img {
				width: 48px;
				height: 48px;
				transition: all 0.3s ease;

				&:hover {
					filter: invert(20%);
				}

				@media (max-width: 991px) {
					width: 32px;
					height: 32px;
				}
			}
		}

		a+a {
			margin-left: 40px;

			@media (max-width: 991px) {
				margin-left: 20px;
			}
		}
	}

	&__lists {
		margin-top: 48px;
	}

	&__list {
		display: flex;
		flex-wrap: wrap;

		@media (max-width: 575px) {
			flex-direction: column;
		}


		li {
			padding: 0 20px 20px 0;
		}
	}

	&__link {
		line-height: 18px;
		text-align: center;
		transition: all 0.3s ease;
		color: #231E2E;
		opacity: 0.5;

		&:hover {
			opacity: 1;
			color: $color-primary;
		}
	}

	&__bottom {
		margin-top: 80px;
		display: flex;
		justify-content: space-between;

		@media (max-width: 575px) {
			justify-content: flex-start;
			flex-direction: column;
			margin-top: 45px;
		}
	}

	&__box {
		display: flex;

		@media (max-width: 991px) {
			align-items: center;
			margin-right: 25px;
		}

		@media (max-width: 575px) {
			flex-direction: column;
			align-items: flex-start;
		}
	}

	&__info {
		margin-right: 80px;

		@media (max-width: 991px) {
			margin-right: 25px;
		}

		@media (max-width: 575px) {
			margin: 0 0 25px 0;
		}

		a,
		span {
			font-weight: 700;
			color: #211F2E;
			opacity: 0.4;

			@media (max-width: 767px) {
				font-size: 12px;
			}

			@media (max-width: 575px) {
				font-size: 16px;
			}
		}

		a {
			transition: all 0.3s ease;

			&:hover {
				color: #E4002B;
				opacity: 1;
			}
		}

		p {
			margin-top: 6px;
			font-size: 12px;
			color: #211F2E;
			opacity: 0.4;

			@media (max-width: 767px) {
				font-size: 10px;
			}

			@media (max-width: 575px) {
				font-size: 12px;
			}
		}
	}

	&__by {
		font-weight: 700;
		color: #211F2E;
		opacity: 0.4;

		@media (max-width: 767px) {
			font-size: 12px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
		}

		a {
			color: #211F2E;
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}
	}
}