.auth {
	background-image: url('../img/bg-auth.jpg');
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
	height: 100vh;
	min-height: 800px;
	display: flex;
	align-items: center;

	&__form {
		background-color: #fff;
		border-radius: 24px;
		padding: 40px;

		@media (max-width: 767px) {
			padding: 26px 20px;
		}

		@media (max-width: 355px) {
			padding-left: 15px;
			padding-right: 15px;
		}
	}

	&__content {
		position: relative;
		margin-top: 42px;
		display: grid;
		grid-gap: 15px;
		z-index: 1;

		@media (max-width: 767px) {
			margin-top: 30px;
		}

		&--registration {
			grid-template-columns: repeat(2, 1fr);

			@media (max-width: 767px) {
				grid-template-columns: repeat(1, 1fr);
			}
		}
	}

	&__box {
		display: flex;
		justify-content: space-between;
		margin-top: 42px;

		@media (max-width: 767px) {
			margin-top: 5px;
		}

		.checkbox {
			span {
				position: absolute;
				left: 0;
			}

			i {
				@media (min-width: 768px) {
					padding-left: 35px;
				}
			}
		}
	}

	&__link {
		font-size: 19px;
		text-decoration-line: underline;
		color: #ADADB0;

		@media (max-width: 767px) {
			font-size: 14px;
			text-align: center;
		}

		@media (max-width: 575px) {
			margin-top: 9px;
		}

		&:hover {
			text-decoration: none;
		}
	}

	&__text {
		font-size: 18px;
		line-height: 120%;
		color: #222025;
		display: flex;
		align-items: center;
		padding: 0 50px;

		@media (max-width: 991px) {
			padding: 0 0 0 30px;
		}

		@media (max-width: 767px) {
			padding: 0;
			font-size: 12px;
		}
	}

	&__desc {
		font-size: 14px;
		line-height: 16px;
		color: #ADADB0;
		display: flex;
		align-items: center;
		padding: 0 100px 0 50px;

		@media (max-width: 991px) {
			padding: 0 0 0 30px;
		}

		@media (max-width: 767px) {
			margin-top: 17px;
			padding: 0;
		}
	}

	&__caption {
		max-width: 428px;

		@media (max-width: 767px) {
			max-width: 265px;
		}
	}

	&__title {
		font-weight: 800;
		font-size: 42px;
		line-height: 100%;
		color: #222025;

		@media (max-width: 767px) {
			font-size: 24px;
			line-height: 24px;
		}
	}

	&__subtitle {
		font-size: 19px;
		line-height: 24px;
		color: #222025;
		margin-top: 29px;

		@media (max-width: 767px) {
			margin-top: 16px;
			font-size: 14px;
			line-height: 16px;
		}

		a {
			display: block;
			color: #222025;
			transition: all 0.3s ease;

			&:hover {
				color: $color-primary;
			}
		}
	}

	&__back {
		margin-top: 34px;
		font-size: 19px;
		text-align: center;
		color: #222025;
		transition: all 0.3s ease;

		@media (max-width: 767px) {
			font-size: 14px;
		}

		&:hover {
			color: $color-primary;
		}
	}

	&__btn {
		padding: 25px 40px;

		&--entry {
			margin-top: 42px;

			@media (max-width: 767px) {
				margin-top: 9px;
			}
		}

		&--registration {
			@media (max-width: 767px) {
				margin-top: 9px;
				order: 1;
			}
		}

		&--passcode {
			margin-top: 27px;
		}
	}
}