.intro {
	height: 100vh;
	min-height: 800px;
	display: flex;
	align-items: center;

	&__content {
		display: flex;
		align-items: center;
		justify-content: space-between;
		max-width: 1060px;
		margin: 0 auto;
		padding: 0 15px;
	}

	&__desc {
		margin-top: 20px;
		font-size: 19px;
		line-height: 120%;
		color: #ADADB0;

		@media (min-width: 992px) {
			max-width: 455px;
		}

		@media (max-width: 575px) {
			font-size: 16px;
			line-height: 18px;
			color: #222025;
		}
	}

	&__btn {
		margin-top: 35px;
		padding: 25px 40px;
	}

	&__img {
		position: relative;
		width: 100%;
		max-width: 520px;
		height: 387px;
		overflow: hidden;
		transform: skew(-15deg) rotate(-10deg);
		border-radius: 50%;
		z-index: -1;
		transition: all .3s ease;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		@media (max-width: 991px) {
			display: none;
		}
	}
}