.applications {
	padding-top: 162px;

	@media (max-width: 767px) {
		padding-top: 90px;
	}

	&__caption {
		display: flex;
		align-items: center;
		justify-content: space-between;
	}

	&__request {
		padding: 18px 25px;

		@media (max-width: 991px) {
			display: none;
		}
	}

	&__content {
		margin-top: 32px;
	}

	&__top {
		display: flex;
		align-items: center;
		justify-content: space-between;

		@media (max-width: 991px) {
			white-space: nowrap;
			overflow-x: auto;
			padding-bottom: 20px;
			padding: 0 5px 20px;
		}
	}

	&__button {
		color: #222025;
		padding: 12px 22px;

		&:hover {
			color: #fff;

			&::before {
				background-color: $color-primary;
			}
		}

		&::before {
			background-color: #F7F7F9;
		}

		&--active.btn {
			color: #fff;
			pointer-events: none;

			&::before {
				background-color: $color-primary;
			}
		}

		&--filter {
			padding-left: 65px;

			@media (max-width: 991px) {
				margin-left: 7px;
			}

			&::after {
				content: '';
				background-image: url('../img/svg/filter.svg');
				background-repeat: no-repeat;
				background-position: center;
				background-size: cover;
				position: absolute;
				top: 50%;
				left: 20px;
				transform: translateY(-50%);
				width: 24px;
				height: 24px;
			}
		}
	}

	&__button+&__button {
		margin-left: 7px;
	}

	&__table {
		margin-top: 42px;
	}

	&__items {
		display: grid;
		grid-template-columns: 300px 130px repeat(5, 1fr);
		grid-gap: 30px;
		border-bottom: 1px solid #DCDCDF;
		padding-bottom: 20px;

		@media (max-width: 1200px) {
			grid-template-columns: 220px 110px repeat(5, 1fr);
			grid-gap: 10px;
		}

		@media (max-width: 991px) {
			grid-template-columns: repeat(3, 1fr);
			grid-gap: 20px;
		}

		@media (max-width: 380px) {
			grid-template-columns: repeat(2, 1fr);
		}
	}

	&__item {
		&:first-child {
			@media (max-width: 991px) {
				grid-column: 1/4;
			}

			@media (max-width: 380px) {
				grid-column: 1/3;
			}

			.applications__title {
				@media (max-width: 991px) {
					display: none;
				}
			}
		}
	}

	&__items+&__items {
		margin-top: 24px;

		@media (max-width: 991px) {
			margin-top: 30px;
		}
	}

	&__subtitle {
		margin-bottom: 24px;
		font-weight: 700;
		color: #adadb0;

		@media (max-width: 575px) {
			font-size: 14px;
			margin-bottom: 15px;
		}

		&--dnone {
			display: none;

			@media (max-width: 991px) {
				display: block;
			}
		}
	}

	&__desc {
		@media (max-width: 1200px) {
			font-size: 14px;
		}
	}

	&__box {
		display: flex;

		@media (max-width: 991px) {
			align-items: center;
		}
	}

	&__img {
		margin-right: 16px;
		width: 100%;
		max-width: 72px;
		height: 72px;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
			background-color: #f7f7f9;
			border-radius: 10px;
			padding: 5px 11px;
		}
	}

	&__link {
		display: block;
		margin-top: 6px;
		color: $color-primary;
		font-size: 13px;
		transition: all 0.3s ease;

		&:hover {
			color: #211f2e;
			opacity: .4;
		}
	}
}