.popup {
	position: fixed;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, 0.8);
	top: 0;
	left: 0;
	opacity: 0;
	visibility: hidden;
	overflow-y: auto;
	overflow-x: hidden;
	transition: all 0.3s ease 0s;
	z-index: 20;

	&.open {
		opacity: 1;
		visibility: visible;
	}

	&.open .popup__content {
		transform: none;
	}

	&__body {
		min-height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		transition: all 0.3 ease 0s;
	}

	&__content {
		background-color: #fff;
		background-size: cover;
		background-position: center;
		background-repeat: no-repeat;
		color: #000;
		width: 100%;
		max-width: 1080px;
		position: relative;
		transition: transform .3s ease-out;
		transform: translate(0, -70px);
		border-radius: 39px;
		padding: 42px;

		@media (max-width: 991px) {
			border-radius: 39px 39px 0 0;
			padding: 36px 20px;
		}

		&--back {
			max-width: 515px;

			@media (max-width: 575px) {
				margin: 0 5px;
				padding: 36px 10px;
			}
		}
	}

	&__top {
		display: flex;
		justify-content: space-between;
	}

	&__box {
		margin-top: 42px;
		display: flex;

		@media (max-width: 575px) {
			flex-direction: column;
		}
	}

	&__left {
		width: 190px;

		@media (max-width: 575px) {
			width: 100%;
		}
	}

	&__close {
		img {
			width: 30px;
			height: 30px;
		}
	}

	&__img {
		height: 300px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: 12px;
		}
	}

	&__swiper2 {
		margin-top: 20px;

		.swiper-slide {
			@media (max-width: 575px) {
				width: auto !important;
			}
		}

		.swiper-slide-thumb-active {
			.popup__img {
				position: relative;
				width: 40px;
				height: 44px;
			}
		}

		.popup__img {
			position: relative;
			width: 35px;
			height: 39px;
			overflow: hidden;
			transform: skew(-15deg) rotate(-10deg);
			border-radius: 50%;
			z-index: -1;
			transition: all .3s ease;
			cursor: pointer;
		}
	}

	&__right {
		margin-left: 30px;

		@media (max-width: 575px) {
			margin: 30px 0 0 0;
			display: flex;
			flex-direction: column;
		}
	}

	&__inner {
		&:first-child {
			order: 2;
			margin-top: 30px;
		}
	}

	&__inner+&__inner {
		@media (min-width: 576px) {
			margin-top: 32px;
		}
	}

	&__title {
		@media (max-width: 991px) {
			font-size: 30px;
			line-height: 30px;
		}

		&--back {
			font-weight: 700;
			font-size: 42px;
			line-height: 42px;
			text-align: center;
			color: #222025;

			@media (max-width: 575px) {
				font-size: 28px;
				line-height: 30px;
			}
		}
	}

	&__subtitle {
		font-weight: 700;
		font-size: 25px;
		color: #222025;
	}

	&__items {
		margin-top: 24px;
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		grid-gap: 16px 30px;

		@media (max-width: 991px) {
			grid-template-columns: repeat(2, 1fr);
		}

		@media (max-width: 767px) {
			grid-template-columns: repeat(1, 1fr);
		}
	}

	&__item {
		&--w100 {
			@media (min-width: 576px) {
				grid-column: 2/4;
			}
		}
	}

	&__label {
		font-size: 14px;
		color: #ADADB0;
	}

	&__value {
		margin-top: 6px;
		line-height: 18px;
		color: #222025;
	}

	&__holder {
		margin-top: 24px;

		.buttons {
			margin-top: 40px;
		}
	}

	&__desc {
		max-width: 305px;
		margin: auto;
		font-size: 19px;
		line-height: 24px;
		text-align: center;
		color: #222025;

		@media (max-width: 575px) {
			font-size: 16px;
			line-height: 20px;
		}
	}
}